<template>
    <div class = 'main-container roberyPage'>
        <h1 class ='title'>Painel Administrativo</h1>
        <div class = 'content-container'>
            <header>
                <h1>Roubos Criados</h1>

                <img v-if = '!create_page' class = 'plusIcon  ' :src = "require(`../../assets/plus.svg`)" alt="" @click = 'create_page = !create_page' v-bind:class = '{FastFade: !create_page}'>
                <img v-if = 'create_page' class = 'plusIcon  ' :src = "require(`../../assets/backArrow.svg`)" alt="" @click = 'create_page = !create_page' v-bind:class = '{FastFade: create_page}'>
            </header>

            <div class = 'robberyUnits-container' v-if = '!create_page'>
                <div class = 'robberyUnit' v-for = '(robbery, index) in robberies' v-bind:key = 'index' v-bind:class = '{FastFade: !create_page}'>
                    <div class = 'historic-data' style="background-color: #361955; width: 25%">
                        {{robbery.preset}}
                    </div>

                    <div class = 'historic-data' style="width: 50%">
                        x: {{robbery.cds.x}} y: {{robbery.cds.y}} z: {{robbery.cds.z}}
                    </div>

                    <div class = 'historic-data'  style="width: 25%" >
                        <img class = 'trashIcon' :src="require(`../../assets/trash.svg`)" alt="" @click = 'deleteRobbery(robbery.count, index)'>
                    </div>
                </div>
            </div>

            <div v-if = 'create_page' class = 'robberyCreate' v-bind:class = '{FastFade: create_page}'>
                 <div class = 'robberyUnit' >
                    <div class = 'selectContainer'>
                        <select class = 'selectPreset' title = 'Escolha o preset.' >
                            <option value="" disabled selected>Selecione o Preset</option>
                            <option v-for = '(preset, index) in create_presets_name'  v-bind:key = 'index' :value="preset">{{preset}}</option>
                        </select>
                    </div>

                    <input v-model = 'create_cds' class = 'cdsInput' style="width: 70%" placeholder="Digite as coordenadas.">
                    
                    <div class = 'getLocalCdsBttn' title = 'Pegar localização atual' @click = 'getUserCds()'>
                        <img :src="require(`../../assets/loc.svg`)" alt="">
                    </div>
                </div>

                <div class = 'addRobberyBttn' @click = 'addRobbery()'>
                    Adicionar Roubo
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                create_cds: '',
                create_presets_name: [],
                create_page: false,

                got_presets: false
            }   
        },

        watch: {
            create_page() {
                if (this.create_page) {
                    this.getPresetsName()
                }
            }
        },

        methods: {
            getUserCds() {
                let options = {
                    method: 'POST',
                    body:  JSON.stringify({"cb": "getCoords"})
                }

                fetch(this.$store.state.url + '/suricato_robberies_API', options)
                .then(async response => {
                    let obj = await response.json()

                    this.create_cds = `${obj.x} ${obj.y} ${obj.z}`

                })
            },

            addRobbery() {
                let preset = document.querySelector('.selectPreset').value 

                let cds_obj = {}
                cds_obj['x'] = this.create_cds.split(' ')[0]
                cds_obj['y'] = this.create_cds.split(' ')[1]
                cds_obj['z'] = this.create_cds.split(' ')[2]


                let options = {
                    method: 'POST',
                    body:  JSON.stringify({"cb": "addRobbery", preset, cds: cds_obj})
                }

                fetch(this.$store.state.url + '/suricato_robberies_API', options)
                .then(async response => {
                    let obj = await response.json()

                    if (obj.status) {
                        this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: obj.msg})

                        this.create_page = false

                        this.$store.commit('getRobberies')
                    } else {
                        this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: obj.msg})
                    }
                })
            },

            deleteRobbery(count, index) {
                let options = {
                    method: 'POST',
                    body:  JSON.stringify({"cb": "remRobbery", count})
                }

                fetch(this.$store.state.url + '/suricato_robberies_API', options)
                .then(async response => {
                    let obj = await response.json()

                    if (obj.status) {
                        this.$store.commit('removeRobbery', index)
                        
                        this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: obj.msg})
                    } else {

                        this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: obj.msg})
                    }
                })
            },

            getPresetsName() {
                let options = {
                    method: 'POST',
                    body:  JSON.stringify({"cb": "getPresetsName"})
                }

                fetch(this.$store.state.url + '/suricato_robberies_API', options)
                .then(async response => {
                    let obj = await response.json()

                    this.create_presets_name = obj

                })

            }

        },

        computed: {
            robberies() {
                return this.$store.state.admin.robberies
            },
        }

    }
</script>

