<template>
    <div class = 'configUnit' >
        <header>
            <h1>Animação</h1>
            <div>
                <img class = 'goToSettingsTimerIcon' @click = 'goToSettingsTimer($event)' data-opened = '0' :src="require(`../../../assets/timer.svg`)" style="margin-right: 10px">
                <img class = 'goToSettingsIcon' @click = 'goToSettings($event)' data-opened = '0' :src="require(`../../../assets/settings.svg`)" alt="">
            </div>
        </header>

        <main v-if = 'checkBox'  v-bind:class = '{FastFade: !settings}'>
            <div class = 'chooseUnit'  v-bind:class = '{activeOption: preset_data.needmakinganim}' @click = 'changeCheckSettings($event, "1")'>
                <img :src="require(`../../../assets/arrow.svg`)"  alt="">
            </div>

            <div class = 'chooseUnit' v-bind:class = '{activeOption: !preset_data.needmakinganim}' @click = 'changeCheckSettings($event, "2")'>
                <img style = 'height: 50px' :src="require(`../../../assets/error.svg`)"  alt="" data-type =  'cancel' >
            </div>
        </main>

        <div class = 'settingsPart'  v-if = 'settings'  v-bind:class = '{FastFade: settings}'>
            <textarea v-model = 'preset_data.animation' maxlength="30" class = 'configInput1' placeholder="Nome da animação" name="name"></textarea >
        </div>

        <div class = 'settingsPart'  v-if = 'settingsTimer'  v-bind:class = '{FastFade: settingsTimer}'>
            <input v-model = 'preset_data.animationTime'  class = 'configInput1' placeholder="Tempo da animação" />
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                settings: false,
                settingsTimer: false,
                checkBox: true
            }
        },

        props: {
            preset_data: Object
        }, 

        methods: {
            goToSettings(event) {
                let element = event.currentTarget
                let other = document.querySelector('.goToSettingsTimerIcon')
                
                if (element.dataset.opened == '1') {
                    element.dataset.opened = '0'
                    
                    this.settingsTimer = false
                    this.settings = false
                    this.checkBox = true
                } else {
                    element.dataset.opened = '1'
                    other.dataset.opened = '0'

                    this.settingsTimer = false
                    this.checkBox = false
                    this.settings = true
                }

            },

            changeCheckSettings(event, checkOption) {
                if (checkOption == '1') {
                    this.preset_data.needmakinganim = true

                } else {
                    this.preset_data.needmakinganim = false

                }
            },

            goToSettingsTimer(event) {
                let element = event.currentTarget
                let other = document.querySelector('.goToSettingsIcon')

                if (element.dataset.opened == '1') {
                    element.dataset.opened = '0'
                    

                    this.settingsTimer = false
                    this.settings = false
                    this.checkBox = true
                } else {
                    element.dataset.opened = '1'
                    other.dataset.opened = '0'

                    this.settings = false
                    this.checkBox = false
                    this.settingsTimer = true
                }
            }

        }
    }
</script>

