<template>
    <div class = 'admin-page page'>
        <aside class = 'menuAside'>
            <div class = 'buttonsMenu-container'>

                <div class="menuUnit">
                    <div class = 'menuBttn activeMenuBttn' >
                        <img  :src="require(`../assets/wrench.svg`)" alt="">
                    </div>
                </div>

            </div>

            <div class = 'exitBttn'>
                <img :src="require(`../assets/exit.svg`)"  alt="">
            </div>
           
        </aside>
        
        <main class = 'mainContent'>
            <Bandit v-if = 'is_bandit' :data = 'bandit' :current_key = 'current_key' :name = 'bandit.name'/>
            <Police v-if = '!is_bandit' :data = 'police' :current_key = 'current_key'  :name = 'police.name'/>
            <div class = 'backStyle'></div>
        </main>
    </div>
</template>

<script>

import Bandit from './components/bandit/Bandit'
import Police from './components/police/Police'


export default {
    data() {
        return {
            is_bandit: false,
            current_key: '',

            bandit: {
                bandit_max: 0,
                bandits: [],
                hostages: [],
                noInvolvement: [],
                projection: [],
                name: ''
            },

            police: {
                name: '',
                needcops: 3,
                key: 1,
                bandits: 2,
                hostages: 0,
                nusers: [],
                cops: []
            }
        }
    },
    components: {
        Bandit,
        Police
    },

    mounted() {
        this.listener = window.addEventListener('message', ({data}) => {
            if (data.action == 'BanditsTeam') {
                this.bandit = {
                    bandit_max: 0,
                    bandits: [],
                    hostages: [],
                    noInvolvement: [],
                    projection: [],
                    name: ''
                }

                this.$store.state.Functions.showPainel('clientPainel')

                this.is_bandit = true
                this.bandit.projection = data.projection
                this.bandit.noInvolvement = data.nusers
                this.bandit.bandit_max = data.bandlimit
                this.bandit.name = data.name
                this.current_key = data.key

                for (let i in this.bandit.noInvolvement) {
                    if (this.bandit.noInvolvement[i].fixed) {
                        this.bandit.bandits.push(this.bandit.noInvolvement[i])
                        this.bandit.noInvolvement.splice(i, 1)
                    }
                }
            } 
            
            if (data.action == 'CopsTeam') {

                this.current_key = data.key

                this.police = data
                this.police.cops = []

                this.is_bandit = false

                this.$store.state.Functions.showPainel('clientPainel')
            }

        });


        
    }
}
</script>

