<template>
    <div class = 'playersConfig-container'>
        <header>
            <h1 style="border-right: solid 1px rgba(255, 255, 255, 0.1)">Invasores Necessários</h1>
            <h1 style="border-right: solid 1px rgba(255, 255, 255, 0.1)">Policiais Necessários</h1>
            <h1>Limite de Reféns</h1>
        </header>

        <div class = 'preset' v-for = '(projection, index) in preset_data.projection' v-bind:key = 'index'>
            <div class = 'invadersNumber-container' style="border-right: solid 1px rgba(255, 255, 255, 0.1)">
                <label class="switch">
                    <input  @click = 'disableProjection($event)'  :data-index = 'index' type="checkbox" v-if = '!projection.disable' checked>
                    <input   @click = 'disableProjection($event)' :data-index = 'index' type="checkbox" v-if = 'projection.disable'>

                    <span class="slider round"></span>
                </label>

                <p>{{index + 1}}</p>
            </div>
            <input type="number" class = 'projection_cops_input' v-model = 'projection.cops' style="border-right: solid 1px rgba(255, 255, 255, 0.1)">
            <input type="number" v-model = 'projection.hostages'>
        </div>

        <footer>
            <h1 @click = 'addProjection()'>+</h1>
            <h1 @click = 'removeProjection()' style="margin-bottom: 8px">-</h1>
        </footer>
    </div>
</template>

<script>
    export default {
        

        props: {
            preset_data: Object
        },

        methods: {
            addProjection() {
                this.preset_data.projection.push({disable: false, cops: 1, hostages: 0})
            },
            removeProjection() {
                if (this.preset_data.projection.length <= 1) {
                    return
                }

                this.preset_data.projection.splice(-1)
            },
            
            disableProjection(event) {
                let element = event.currentTarget
                let index = element.dataset.index

                this.preset_data.projection[index].disable = !element.checked

            }
        },

        mounted() {
            var foo = document.querySelectorAll(".projection_cops_input");

            var f = maxNumber(999, 1);

            for (let i in foo) {
                if (typeof foo[i] == 'object') {
                    foo[i].addEventListener('keyup', f);
                    foo[i].addEventListener('blur', f);
                }
            }

            function maxNumber(max, min)
            {
                var running = false;
                
                return function () {
                    if (running) return;
                    
                    running = true;
                    
                    if (parseFloat(this.value) > max) {
                        this.value = max;
                    } else if (parseFloat(this.value) < min) {
                        this.value = min
                    }

                    running = false;
                };
            }
        }
    }
</script>

