<template>
    <div class = 'messageNotify-container'>
        <div class="messageNotify notify_appear" v-for = '(notify, index) in notifies' :key = 'index' :class = '"MN"+ index'>
            <img :src="require(`../assets/${notify.icon}.svg`)" alt="">

            <p>{{notify.content}}</p>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            notifies: [ ],

        }
    },
    methods: {
        notify(data) {
            let obj = {}

            if (data.style == 'negado') {
                obj['icon'] = 'x'
            } else if (data.style == 'sucesso') {
                obj['icon'] = 'check'
            } else {
                obj['icon'] = 'warn'
            }

            obj['content'] = data.content

            let index = this.notifies.push(obj) - 1

            this.delete(index, data.sleep)
        },

        delete(index, sleep) {
            setTimeout(() => {
                let elementClass = "MN" + index
                let element = document.querySelector(`.${elementClass}`)
                element.classList.remove('notify_appear')
                element.classList.add('notify_disappear')
                
                setTimeout(() => {
                    element.remove()
                }, 990)
            }, sleep)
        }
    },

    mounted() {
        this.$root.$on('messageNotify', (data) => {
            this.notify(data)
        })

        this.listener = window.addEventListener('message', ({data}) => {
            if (data.action == 'NotifyMessage') {
                this.notify(data)
            }

            if (data.action == 'RegisterRequest') {

                let notify = document.createElement('div')
                notify.classList.add('notify')
                notify.classList.add('notify_appear')
                notify.classList.add('notify_id' + data.id)

                notify.innerHTML = `
                    <p class = 'message'>${data.msg}</p>

                    <div class = 'bindContainer'>
                        <div class = 'bind'>
                            <h1>Y</h1>
                            <p>SIM</p>
                        </div>

                        <div class = 'bind'>
                            <h1>U</h1>
                            <p>NÃO</p>
                        </div>
                    </div>
                `

                let container = document.querySelector('.messageNotify-container')

                container.appendChild(notify)
            }

            if (data.action == 'DeleteRequest') {

                let notify = document.querySelector('.notify_id' + data.id)

                notify.classList.remove('notify_appear')
                notify.classList.add('notify_disappear')

                setTimeout(() => {
                    notify.remove()
                }, 990)
            }
        }, false)

        
    }
}
</script>

<style scoped>
.messageNotify-container {
    position: absolute;
    z-index: 1;
    right: 0px;
    top: 150px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    padding: 20px;
}

.messageNotify {
    background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(4,3,46,1) 10%, rgba(23,15,72,1) 90%, rgba(255,255,255,0) 100%);
    padding: 15px 30px;

    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.137));

    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
    border-radius: 10px;
}

.messageNotify p {
    font-family: 'Barlow';
    color: rgba(255, 255, 255, 0.603);
    text-align: center;
    height: 20px;
}

.messageNotify img {
    height: 20px;
    margin-right: 20px;
    opacity: 80%;
}

</style>