const mutations = {
    changeScoreboardPlayers(state, data) {
        state.admin.current_scoreboard_players = state.admin.actions_historics[data.index].players[data.team]
    },
    changeScoreboardName(state, name) {
        state.admin.current_scoreboard_name = name
    },

    changeScoreboardStatus(state, status) {
        state.admin.current_scoreboard_status = status
    },
    
    changeCurrentPresetIndex(state, index) {
        state.admin.current_robery_index = index
    },
    presetBackup(state, index) {
        state.admin.preset_backup = JSON.parse(JSON.stringify(state.admin.robery_presets[index]))
    },

    createPreset(state) {
        state.admin.robery_presets.push(
            {
                "needmakinganim": false,
                "isminigame": true,
                "minigame": "1",
                "awarddarkmoney": false,
                "cooldown": 0,
                "items": [],
                "iswanted": true,
                "awardmax": 0,
                "awardmin": 0,
                "name": "Novo Preset",
                "permission": "",
                "ispermission": true,
                "projection": [
                    {
                    "cops": "1",
                    "disable": false,
                    "hostages": 1
                    }
                ],
                "animation": "",
                "animationTime": 0
            },
        )
    },
    
    savePreset(state, data) {
        state.admin.robery_presets[data.index] = data.obj
    },
    
    no() {
        console.log('[ NO ]')
    },
    
    getHistoric(state) {
        let options = {
            method: 'POST',
            body: JSON.stringify({"cb": "openHistoricRobberys"})
        }

        fetch(state.url + '/suricato_robberies_API', options) 
        .then(async response => {
            let obj = await response.json()

            state.admin.actions_historics = obj

            return 
        })
    },

    removeAdminNotify(state, index) {
        state.admin.notify_roberies.splice(index, 1)
    },

    getAdminNotifies(state) {
        let options = {
            method: 'POST',
            body: JSON.stringify({"cb": "openNotify"})
        }

        fetch(state.url + '/suricato_robberies_API', options) 
        .then(async response => {
            let obj = await response.json()

            state.admin.notify_roberies = obj

            return 
        })
    },

    getPresets(state) {
        let options = {
            method: 'POST',
            body: JSON.stringify({"cb": "getPresets"})
        }

        fetch(state.url + '/suricato_robberies_API', options) 
        .then(async response => {
            let obj = await response.json()
            
            state.admin.robery_presets = obj
            
            return 
        })
    },

    deletePreset(state) {
        state.admin.robery_presets.splice(state.admin.current_robery_index, 1)
    },

    getRobberies(state) {
        
        let options = {
            method: 'POST',
            body: JSON.stringify({"cb": "getRobberies"})
        }
        
        fetch(state.url + '/suricato_robberies_API', options)
        .then(async response => {
            let obj = await response.json()
            
            state.admin.robberies = obj

            
        })
    },

    addRobbery(state, data) {
        state.admin.robberies.push(data)
    },

    removeRobbery(state, index) {
        state.admin.robberies.splice(index, 1)
    },

    setCurrentPainel(state, name) {
        state.current_painel = name
    },

    setCurrentRobberyKey(state, key) {
        state.currentRobberyKey = key
    }

   


}

export default mutations