<template>
    <div class = 'main-container presetPage'>
        <h1 class ='title'>Configurar Novo Roubo</h1>
        <div class="content-container">
            <header>
                <h1>Novo Preset</h1>
            </header>

            <div class = 'robberies-container'>
                <div class = 'addRobbery' @click = 'createPreset()'>
                    <img :src="require(`../../assets/plus.svg`)"  alt="">
                    <!-- <p>Novo Preset</p> -->
                </div>

                <div class = 'createdRobbery fade' v-for = '(preset, index) in presets' v-bind:key = 'index'>
                    <img class = 'robImg' src="https://cdn.discordapp.com/attachments/864220360416952341/905927783434297494/unknown.png" alt="">

                    <div class = 'text-container'>
                        <h1>{{preset.name}}</h1>
                        <p>
                            <span v-if = 'preset.awarddarkmoney == "1"'>Dinheiro Sujo</span>
                            <span v-else >Dinheiro Limpo</span>
                            <br> R$ {{preset.awardmin}} - {{preset.awardmax}}
                        </p>
                    </div>

                    <div class = 'edit-robbery' :data-index = 'index' @click = 'editPreset($event)'>
                        <img :src="require(`../../assets/edit.svg`)"  alt="">
                        <p>Editar Preset</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                
            }
        },

        computed: {
            presets() {
                return this.$store.state.admin.robery_presets
            }
        },

        methods: {
            createPreset() {
                this.$store.commit('createPreset')

                this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: 'Preset criado com sucesso!'})
            },

            editPreset(event) {
                let element = event.currentTarget

                this.$store.commit('changeCurrentPresetIndex', element.dataset.index)

                this.$store.commit('presetBackup', element.dataset.index)

                this.$store.state.Functions.changePage('editPresetPage')
            }
        }
    }
</script>

