export default {

init(data) {
    let config = {
        drop(event, ui) {
            let element = ui.draggable[0]
            let container = event.target

            let name = element.textContent.split(' | ')[0]
            let id = element.textContent.split(' | ')[1]
            let element_key = element.dataset.key

            if (element.dataset.index == 'noInvolvement') {
                if (container.dataset.container == 'robber' && data.bandit_max > data.bandits.length ) {
                    
                    data.bandits.push({name, id})
                    data.noInvolvement.splice(element_key, 1)

                } else if (container.dataset.container == 'hostage') {
                    let projection_index = data.bandits.length

                    if (projection_index > 0) {
                        projection_index--
                    }

                    let projection = data.projection[projection_index]
                    console.log(projection_index, JSON.stringify(projection), '[TESTE:1]')
                    if (data.hostages.length < projection.hostages) {
                        data.hostages.push({name, id})
                        data.noInvolvement.splice(element_key, 1)
                    } else {
                        return 
                    }
                } 
            } else if (element.dataset.index == 'robber') {
                if (element.dataset.fixed == 'true') {
                    return
                }

                if (container.dataset.container == 'noInvolvement') {
                    let projection_index = data.bandits.length

                    if (projection_index > 0) {
                        projection_index--
                    }

                    let projection = data.projection[projection_index]

                    if (data.hostages.length > projection.hostages ) {
                        let dif = data.hostages.length - projection.hostages

                        for (let i = 0; dif > i; i++) {
                            data.noInvolvement.push(data.hostages.splice(0, 1)[0])
                        }
                    }

                    data.bandits.splice(element_key, 1) 
                    data.noInvolvement.push({name, id})
                } else if (container.dataset.container == 'hostage') {
                    let projection_index = data.bandits.length

                    if (projection_index > 0) {
                        projection_index--
                    }

                    let projection = data.projection[projection_index]
                    if (data.hostages.length < projection.hostages) {
                        data.hostages.push({name, id})
                        data.bandits.splice(element_key, 1) 
                    } else {
                        return 
                    }
                }
            } else if (element.dataset.index == 'hostage') {
                if (container.dataset.container == 'noInvolvement') {
                    data.noInvolvement.push({name, id})
                    data.hostages.splice(element_key, 1) 
                } else if (container.dataset.container == 'robber' && data.bandit_max > data.bandits.length ) {
                    data.bandits.push({name, id})
                    data.hostages.splice(element_key, 1)
                } 
            }
        }
    }

    return config
}























}