const functions = {
    changeMenu(event, containerClass) {
        let element = event.currentTarget

        let old_menu = document.querySelector('.activeMenuBttn')
        old_menu.classList.remove('activeMenuBttn')
        element.classList.add('activeMenuBttn')
        
        this.changePage(containerClass)
    },

    changePage(containerClass) {
        let active_container = document.querySelector(`.${containerClass}`)

        let containers = document.querySelectorAll('.containerPage')

        for (let i in containers) {
            if (typeof containers[i] == 'object') {
                containers[i].classList.remove('fade')
                containers[i].style.display = 'none'
            }
        }

        active_container.classList.add('fade')
        active_container.style.display = 'flex'
    },

    changePageGame(containerClass) {
        let active_container = document.querySelector(`.${containerClass}`)

        let containers = document.querySelectorAll('.containerPageGame')

        for (let i in containers) {
            if (typeof containers[i] == 'object') {
                containers[i].classList.remove('fade')
                containers[i].style.display = 'none'
            }
        }

        active_container.classList.add('fade')
        active_container.style.display = 'flex'
    },

    showPainel(containerClass) {
        let active_container = document.querySelector(`.${containerClass}`)

        active_container.classList.remove('disappear')
        active_container.classList.add('appear')

        active_container.style.display = 'flex'
    },

    hidePainel(containerClass) {
        let active_container = document.querySelector(`.${containerClass}`)

        active_container.classList.add('disappear')
        active_container.classList.remove('appear')

        setTimeout(() => {
            active_container.style.display = 'none'
        }, 590)
    },

    closeAllPainel() {
        let containers = document.querySelectorAll('.painel')

        for (let i in containers) {
            if (typeof containers[i] == 'object') {
                containers[i].classList.remove('fade')
                containers[i].classList.add('disappear')
            }
        }

        setTimeout(() => {
            for (let i in containers) {
                if (typeof containers[i] == 'object') {
                    containers[i].style.display = 'none'
                }
            }   
        }, 590)

    },

    goToLoading() {
        let blocked = document.querySelector('.blockEsc') 
        blocked.textContent = '1'

        this.closeAllPainel()   
        setTimeout(() => {
            this.showPainel('loadingPainel')
        }, 700)     
    }

   
    
} 
 

export default functions