<template>
    <div class = 'scoreboardHud' v-if = 'showContainer'> 
        <div class = 'text'>
            <p>{{cops}}</p>
            <h1>X</h1>
            <p>{{bandits}}</p>
        </div>
        <svg width="259" height="40" viewBox="0 0 259 40"  fill="none" xmlns="http://www.w3.org/2000/svg">
            <rect x="259" width="40" height="259" rx="10" transform="rotate(90 259 0)" fill="url(#paint0_linear_0:1)"/>
            <g style="mix-blend-mode:hard-light" opacity="0.6">
            <path d="M116 40L188.743 40L259 40L259 1.87766e-06L116 7.62939e-06L143.978 20L116 40Z" fill="url(#paint1_linear_0:1)" fill-opacity="0.9"/>
            </g>
            <path opacity="0.6" d="M125 40L193.165 40L259 40L259 -1.57504e-06L125 3.8147e-06L151.217 20L125 40Z" fill="url(#paint2_linear_0:1)"/>
            <defs>
            <linearGradient id="paint0_linear_0:1" x1="279" y1="0" x2="279" y2="259" gradientUnits="userSpaceOnUse">
            <stop stop-color="#252836" stop-opacity="0.07"/>
            <stop offset="0.348958" stop-color="#170F48"/>
            <stop offset="0.645833" stop-color="#0C1436"/>
            <stop offset="1" stop-color="#252836" stop-opacity="0.12"/>
            </linearGradient>
            <linearGradient id="paint1_linear_0:1" x1="116" y1="20" x2="259" y2="20" gradientUnits="userSpaceOnUse">
            <stop stop-color="white" stop-opacity="0.934066"/>
            <stop offset="1" stop-color="white" stop-opacity="0"/>
            </linearGradient>
            <linearGradient id="paint2_linear_0:1" x1="125" y1="20" x2="259" y2="20" gradientUnits="userSpaceOnUse">
            <stop stop-color="#6900AC" stop-opacity="0.934066"/>
            <stop offset="1" stop-color="#61009F" stop-opacity="0"/>
            </linearGradient>
            </defs>
        </svg>
    </div>
</template>

<script>
export default {
    data() {
        return {
            showContainer: false,
            cops: 0,
            bandits: 0
        }
    },

    mounted() {
        this.listener = window.addEventListener('message', ({data}) => {
            if (data.action != 'UpdatingHud') {
                return
            }

            if (data.robbery == false) {
                this.showContainer = false
                return
            } else {
                this.showContainer = true
            }
            
            let bandits = 0
            let bandits_arr = data.robbery.bandits

            for (let i in bandits_arr) {
                if (bandits_arr[i].isvivo) {
                    bandits += 1
                }
            }

            let cops = 0
            let cops_arr = data.robbery.cops

            for (let i in cops_arr) {
                if (cops_arr[i].isvivo) {
                    cops += 1
                }
            }

            this.bandits = bandits
            this.cops = cops

           
        })
    }
}
</script>

