<template>
    <div class = 'winContainer' v-bind:class = '{winAnimation_disappear: !show, winAnimation_appear: show}' style = 'display: none'>
        <p>Ação Finalizada</p>
        <h1>{{team}} Venceram</h1>
    </div>
</template>

<script>
export default {
    data() {
        return {
            show: true,
            team: 'Policiais'
        }
    },

    mounted() {
        this.listener = window.addEventListener('message', ({data}) => {
            if (data.action == 'WinnerNotify') {
                document.querySelector('.winContainer').style.display = 'flex'
                this.show = true

                if (data.winner == 'cops') {
                    this.team = 'Policiais'
                } else {
                    this.team = 'Bandidos'
                }

                setTimeout(() => {
                    this.show = false
                }, 10000)
            }
           

        }, false);
    }
}
</script>

