<template>
    <div class = 'configUnit' >
        <header>
            <h1>Permissão Iniciar</h1>
            <img @click = 'goToSettings($event)' data-opened = '0' :src="require(`../../../assets/settings.svg`)" alt="">
        </header>

        <main v-if = '!settings'  v-bind:class = '{FastFade: !settings}'>
            <div class = 'chooseUnit'  v-bind:class = '{activeOption: preset_data.ispermission}' @click = 'changeCheckSettings($event, "1")'>
                <img :src="require(`../../../assets/arrow.svg`)" alt="">
            </div>

            <div class = 'chooseUnit' v-bind:class = '{activeOption: !preset_data.ispermission}' @click = 'changeCheckSettings($event, "2")'>
                <img style = 'height: 50px' :src="require(`../../../assets/error.svg`)"  alt="" data-type =  'cancel' >
            </div>
        </main>
        
        

        <div class = 'settingsPart'  v-if = 'settings'  v-bind:class = '{FastFade: settings}'>
            <textarea v-model = 'preset_data.permission' maxlength="30" class = 'configInput1' placeholder="Nome da Permissão" name="name"></textarea >
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                settings: false,
                publicPath: process.env.BASE_URL
            }
        },

        props: {
            preset_data: Object
        }, 

        methods: {
            goToSettings(event) {
                let element = event.currentTarget
                
                if (element.dataset.opened == '1') {
                    element.dataset.opened = '0'
                    this.settings = false
                } else {
                    element.dataset.opened = '1'
                    this.settings = true
                }

            },

            changeCheckSettings(event, checkOption) {
                if (checkOption == '1') {
                    this.preset_data.ispermission = true

                } else {
                    this.preset_data.ispermission = false

                }
            },

        }
    }
</script>

