<template>
    <div id="app">
        <div class = 'openedPainel' style="display: none" ></div>
        <div class = 'robberyKey' style="display: none" ></div>

        <div class = 'blockEsc' style="display:none">0</div>
        <Admin  class = 'adminPainel painel' style="display: none"/>
        <Client   class = 'clientPainel painel' style="display: none"/>
        <Scoreboard  class = 'gamePainel painel'  style="display: none"/>
        <Loading  class = 'loadingPainel painel' style="display: none"/>
        
        <Notify />
        <Hud />
        <KillFeed />
        <Win />
        <Spectate />
        <HitMarker />

    </div>
</template>

<script>
import Admin from './_admin/Admin'
import Client from './_client-robbery/Client'
import Scoreboard from './_scoreboard/Game'
import Notify from './_notify/Notify'
import Hud from './_hud/Hud'
import KillFeed from './_kill/KillFeed'
import Win from './_win/Win'
import Spectate from './_spectate/Spectate'
import Loading from './_loading/Loading'
import HitMarker from './_hit-marker/HitMarker'

export default {
    data(){
        return {
           
        }
    },
 
    components: {
        Admin,
        Client,
        Scoreboard,
        Notify,
        Hud,
        KillFeed,
        Win,
        Spectate,
        Loading,
        HitMarker
    },
    
    mounted() {
        this.listener = window.addEventListener('message', ({data}) => {
            let action = data.action

            let opened_painel = document.querySelector('.openedPainel')
            let key = document.querySelector('.robberyKey')

            if (action == 'openAdminPainel') {
                opened_painel.textContent = ''
                this.$store.state.Functions.showPainel('adminPainel')
            } else if ( action == 'BanditsTeam' || action == 'CopsTeam'){
                opened_painel.textContent = 'robberyBandit'
                key.textContent = data.key 
            } else if (action == 'ClosePainel') {
                this.$store.state.Functions.closeAllPainel()
            }
        }, false);

        this.listener = window.addEventListener('keydown', function(event) {
            if (event.keyCode == '27') {
                let blocked = document.querySelector('.blockEsc').textContent

                if (blocked == '1') {
                    return
                }

                let opened_painel = document.querySelector('.openedPainel').textContent
                let key = document.querySelector('.robberyKey').textContent

                let containers = document.querySelectorAll('.painel')

                for (let i in containers) {
                    if (typeof containers[i] == 'object') {
                        containers[i].classList.remove('fade')
                        containers[i].classList.add('disappear')
                    }
                }

                setTimeout(() => {
                    for (let i in containers) {
                        if (typeof containers[i] == 'object') {
                            containers[i].style.display = 'none'
                        }
                    }   
                }, 590)

                let options = {
                    method: 'POST',
                    body: JSON.stringify({"cb": "closePainel"})
                }

                if (opened_painel == 'robberyBandit') {
                    options = {
                        method: 'POST',
                        body: JSON.stringify({"cb": "closePainel", key})
                    }
                }

                let active_containerScoreboard = document.querySelector(`.scoreboardPageGame`)

                let containersScoreboard = document.querySelectorAll('.containerPageGame')

                for (let i in containersScoreboard) {
                    if (typeof containersScoreboard[i] == 'object') {
                        containersScoreboard[i].classList.remove('fade')
                        containersScoreboard[i].style.display = 'none'
                    }
                }

                active_containerScoreboard.classList.add('fade')
                active_containerScoreboard.style.display = 'flex'

                let element = document.querySelector('.scoreboardMenuBttn')
                let old_menu = document.querySelector('.surrenderMenuBttn')
                old_menu.classList.remove('activeMenuBttnGame')
                element.classList.add('activeMenuBttnGame')
               
                fetch('http://suricato_robberies/suricato_robberies_API', options) 
            } 
        }, false);
    
    }
}
</script>

<style>

.painel {
    z-index: 111 !important;
}
</style>
