<template>
    <div class = 'main-container editPresetPage'>
        <h1 class ='title'>Configurar Roubo</h1>
        <div class="content-container">
            <div class = 'pageHeader' >
                <h1>Editar Preset</h1>

                <img v-bind:class = '{FastFade: !delete_preset_confirm}' v-if = '!delete_preset_confirm' class = 'trashIcon' :src="require(`../../assets/trash.svg`)" @click = 'delete_preset_confirm = !delete_preset_confirm'>

                <div class = 'trashConfirm-container' v-if = 'delete_preset_confirm'  v-bind:class = '{FastFade: delete_preset_confirm}'>
                    <div class = 'cancelBttn bttn' @click = 'delete_preset_confirm = !delete_preset_confirm'>
                        Cancelar
                    </div>

                    <div class = 'saveBttn bttn' style="margin-right: 0px"  @click = 'deletePreset()'>
                        Apagar
                    </div>
                </div>
            </div>

            <div class = 'config-container'>
                <div class = 'configPart-container'>
                    <PresetName :preset_data = 'preset_data' />

                    <CardMoney  :preset_data = 'preset_data'  />
                    
                    <CardMoneyType  :preset_data = 'preset_data' />

                    <PlayersCard  :preset_data = 'preset_data' />

                    <SettingItem  :preset_data = 'preset_data'   />
            
                    <WantedCard  :preset_data = 'preset_data' />

                    <SettingMiniGame :preset_data = 'preset_data' />

                    <Cooldown  :preset_data = 'preset_data'   />
                    
                    <SettingPerm  :preset_data = 'preset_data' />

                    <SettingAnim :preset_data = 'preset_data'    />

                </div>
            </div>

            <div class = 'bttn-container'>
                <div class = 'cancelBttn bttn' @click = 'back()'>
                    Cancelar
                </div>

                <div class = 'saveBttn bttn' @click = 'save()'>
                    Salvar
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import PlayersCard from './presetCards/PlayersCard.vue'
import SettingMiniGame from './presetCards/SettingMiniGame.vue'
import SettingPerm from './presetCards/SettingPerm.vue'
import PresetName from './presetCards/PresetName.vue'
import CardMoney from './presetCards/CardMoney.vue'
import CardMoneyType from './presetCards/MoneyType.vue'
import SettingItem from './presetCards/SettingItem'
import Cooldown from './presetCards/Cooldown'
import WantedCard from './presetCards/WantedCard'
import SettingAnim from './presetCards/SettingAnim'

export default {
    components: {
        SettingMiniGame,
        PlayersCard,
        SettingPerm,
        PresetName,
        CardMoney,
        CardMoneyType,
        SettingItem,
        Cooldown,
        WantedCard,
        SettingAnim
    },
    
    data() {
        return {
            edit_obj: {},
            delete_preset_confirm: false                
        }
    },

    computed: {
        preset_data() {
            return this.$store.state.admin.robery_presets[this.$store.state.admin.current_robery_index]
        },
       
    },

    methods: {

        save() {

            let presets = this.$store.state.admin.robery_presets

            let name_cont = 0

            for (let i in presets) {
                if (presets[i].name == this.preset_data.name) {
                    name_cont++
                }
            }

            if (name_cont >= 2) {
                
                this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: 'Nome do preset já em uso, escolha outro!'})
                return 
            }

            if (this.preset_data.awardmin >= this.preset_data.awardmax) {
                this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: 'Erro ao salvar. O valor minimo é maior do que o máximo.'})
                return
            }

            this.preset_data['last_name'] = this.$store.state.admin.preset_backup.name

            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": "createPreset", data: this.preset_data})
            }

            fetch(this.$store.state.url + '/suricato_robberies_API', options)
            .then(async response => {
                let obj = await response.json()

                if (obj.status) {
                    this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: obj.msg})
                    this.$store.state.Functions.changePage('presetPage')
                } else {
                    this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: obj.msg})
                }

            })
        },

        back() {
            this.edit_obj = {}
            
            for (let i in this.preset_data) {
                this.preset_data[i] = this.$store.state.admin.preset_backup[i]
            }

            this.$store.state.Functions.changePage('presetPage')

        },

        deletePreset() {
            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": "deletePreset", name: this.$store.state.admin.preset_backup.name})
            }

            fetch(this.$store.state.url  + '/suricato_robberies_API', options)
            .then(async response => {
                let obj = await response.json()

                if (obj.status) {
                    this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: 'Preset apagado com sucesso!'})

                    this.$store.state.Functions.changePage('presetPage')

                    this.$store.commit('deletePreset')

                    this.delete_preset_confirm = false

                } else {
                    
                    this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: 'Não foi possivel apagar o Preset. Roubos em andamento.'})
                }
            })
            .catch((err) => {
            })

        }
        
    }

    
}
</script>

