<template>
    <div class = 'container-kill'>
        <div class = 'kill-notify kill_appear' :class = '"KN" + index' v-for = '(kill, index) in kills' :key = 'index'>
            <p >{{kill.killer}}</p>
            <div class = 'icon killedGunIcon' :style = '{backgroundImage: "url(" + kill.icon + ")"}'></div>
            <p >{{kill.victim}}</p>

            <img class = 'background' :src="require('../assets/notify.svg')" alt="">
        </div>
    </div>
</template>

<script>
export default {
    computed: {
        kills() {
            let data = this.kills_data

            let arr = []

            for (let i in data) {
                arr[i] = {}

                if (data[i].type == 'death') {
                     if (data[i].victim.team == data[i].myteam) {
                        arr[i].victim = data[i].victim.name
                        arr[i].killer = data[i].victim.name
                    } else {
                        if (data[i].victim.team == 'bandits') {
                            arr[i].victim = 'Bandido'
                            arr[i].killer = 'Bandido'
                        } else {
                            arr[i].victim = 'Policial'
                            arr[i].killer = 'Policial'
                        }
                    }
                } else {
                     if (data[i].victim.team == data[i].myteam) {
                        arr[i].victim = data[i].victim.name
                    } else {
                        if (data[i].victim.team == 'bandits') {
                            arr[i].victim = 'Bandido'
                        } else {
                            arr[i].victim = 'Policial'
                        }
                    }

                    if (data[i].killer.team == data[i].myteam) {
                        arr[i].killer = data[i].killer.name
                    } else {
                        if (data[i].killer.team == 'bandits') {
                            arr[i].killer = 'Bandido'
                        } else {
                            arr[i].killer = 'Policial'
                        }
                    }
                }

               

                arr[i].icon = data[i].icon
            }

            return arr
        }
    },
    data() {
        return {
            kills_data: []
        }
    },

    methods: {
        notify(data) {
            let index = this.kills_data.push(data) - 1

            this.delete(index)
        },

        delete(index) {
            setTimeout(() => {
                let elementClass = "KN" + index

                let element = document.querySelector(`.${elementClass}`)

                element.classList.remove('kill_appear')
                element.classList.add('kill_disappear')
                
                setTimeout(() => {
                    element.remove()
                }, 990)
            }, 3000)
        }
    },

    mounted() {
        this.listener = window.addEventListener('message', ({data}) => {
            if (data.action != 'KillFeed') {
                return
            }

           this.notify(data)
            
        })

        

    }
}
</script>

