    position: absolute;
    top: 120px;
    left: 20px;

    height: 100%;

    display: flex;
    flex-direction: column;
}

.kill-notify {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;

    padding: 0px 20px;

    border-radius: 10px;
    
}


.kill-notify .icon {
    width: 45px;
    height: 45px;
    opacity: 100%;
    margin: 0px 15px;
    z-index: 1;

    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.kill-notify p {
  
    font-family: 'Barlow', sans-serif;
    color: rgb(255, 255, 255);
    margin-bottom: 3px;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 90px;

    z-index: 1;
}

.background {
    height: 45px !important;
    position: absolute;
}

</style>