<template>
    <div class = 'main-container scoreboardPage'>
        <h1 class ='title'>Painel Administrativo</h1>
        <div class="content-container">
            <header>
                <h1>Scoreboard - {{name}}</h1>
            </header>

            <div class = 'players-container'>
                 <div class = 'playerUnit' v-for = '(player, index) in players' v-bind:key = 'index' :data-userid = 'player.id'>
                    <div class = 'player-data' style="background-color: #361955;">
                        <p v-if = 'player.isvivo'>Vivo</p>
                        <p v-else>Morto</p>
                    </div>

                    <div class = 'player-data'>
                        {{player.name}}
                    </div>

                    <div class = 'player-data'>
                        Kills: {{player.kills}}
                    </div>

                    <div v-if = 'status' class = 'player-data' style="background-color: #191722; border-right: solid 1px rgba(255, 255, 255, 0.103);" @click = 'userAction(index, "spectateUserInRobberyNow")'>
                        Espectar
                    </div>

                    <div v-if = 'status' class = 'player-data' style="background-color: #191722" @click = 'userAction(index, "teleportToUserInRobberyNow")'>
                        Teletransportar
                    </div>
                </div>
            </div>

            <div class = 'backBttn' @click = 'backToHistoric()'>
                Voltar
            </div>
        </div>
    </div>  
</template>

<script>
    export default {
        computed: {
            name() {
                return this.$store.state.admin.current_scoreboard_name
            },

            players() {
                return this.$store.state.admin.current_scoreboard_players
            },

            status() {
                return this.$store.state.admin.current_scoreboard_status
            }
        },
        methods: {
            backToHistoric() {
                this.$store.state.Functions.changePage('historicPage')
            },

            userAction(index, cb) {
                let options = {
                    method: 'POST',
                    body: JSON.stringify({cb, player: this.players[index]})
                }


                fetch('http://suricato_robberies/suricato_robberies_API', options)
                .then(async response => {
                    let obj = await response.json()

                    if (obj.status) {
                        let options = {
                            method: 'POST',
                            body: JSON.stringify({"cb": "closePainel"})
                        }
                    
                        fetch('http://suricato_robberies/suricato_robberies_API', options) 

                        this.$store.state.Functions.closeAllPainel()
                    }
                })

            }
        }
    }
</script>

