<template>
    <div class = 'configUnit'>
        <header>
            <h1>Mini Game</h1>
            <img @click = 'goToSettings($event)' data-opened = '0' :src="require(`../../../assets/settings.svg`)" alt="">
        </header>

        <main v-if = '!settings' v-bind:class = '{FastFade: !settings}'>
            <div class = 'chooseUnit'  v-bind:class = '{activeOption: preset_data.isminigame}' @click = 'changeCheckSettings($event, "1")'>
                <img :src="require(`../../../assets/arrow.svg`)" alt="">
            </div>

            <div class = 'chooseUnit' v-bind:class = '{activeOption: !preset_data.isminigame}' @click = 'changeCheckSettings($event, "2")'>
                <img style = 'height: 50px' :src="require(`../../../assets/error.svg`)"  alt="" data-type =  'cancel' >
            </div>
        </main>
        

        <div class = 'settingsPart' v-if = 'settings' v-bind:class = '{FastFade: settings}'>
            <div class = 'checkInput'>
                <div class = 'checkBall-container' @click = 'changeSettings($event, "1")' >
                    <div class = 'ball' v-bind:class = "{selectedBallSetting: preset_data.minigame == '1'}"></div> 
                </div>
                <p>Hackear Notebook</p>
            </div>

            <div class = 'checkInput'>
                <div class = 'checkBall-container' @click = 'changeSettings($event,  "2")' >
                    <div class = 'ball' v-bind:class = "{selectedBallSetting: preset_data.minigame == '2'}"></div>
                </div>
                <p>Alinhar Componentes</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            settings: false,
            ball1_active: false,
            ball2_active: false,
            preset_value: '',
            isActive: false,

        }
    },

    props: {
        preset_data: Object
    },  

    methods: {
        goToSettings(event) {
            let element = event.currentTarget
            
            if (element.dataset.opened == '1') {
                element.dataset.opened = '0'
                this.settings = false
            } else {
                element.dataset.opened = '1'
                this.settings = true
            }

        },

        changeCheckSettings(event, checkOption) {
            if (checkOption == '1') {
                this.preset_data.isminigame = true
                this.activeOp = true
            } else {
                this.preset_data.isminigame = false
                this.activeOp = false
            }

        },

        changeSettings(event, type) {
            let element = event.currentTarget


            if (type == '1') {
                this.preset_data.minigame = '1'

            } else {
                this.preset_data.minigame = '2'

            }

        }
    }
}
</script>

