export default {

init(data) {
    let config = {
        drop(event, ui) {
            let element = ui.draggable[0]
            let container = event.target

            let name = element.textContent.split(' | ')[0]
            let id = element.textContent.split(' | ')[1]
            let element_key = element.dataset.key

            if (element.dataset.index == 'nusers') {
                if (container.dataset.container == 'cops' && data.needcops > data.cops.length ) {
                    
                    data.cops.push({name, id})
                    data.nusers.splice(element_key, 1)

                } 
            } else if (element.dataset.index == 'cops') {
                if (container.dataset.container == 'nusers' ) {
                    
                    data.nusers.push({name, id})
                    data.cops.splice(element_key, 1)

                } 
            }
                
        }
    }

    return config
}























}