<template>
    <div class = 'admin-page page'>
        <aside class = 'menuAside'>
            <div class = 'buttonsMenu-container'>

                <div class="menuUnit" v-for = '(menu, index) in menus' v-bind:key = 'index' >
                    <div :class = 'menu.class' @click = 'changeMenu($event, menu.element, menu.function)'>
                        <img :src="require(`../assets/` + menu.image)" alt="">
                    </div>
                </div>

            </div>

            <div class = 'exitBttn' @click = 'closePainel()'>
                <img  src="../assets/exit.svg"  alt="">
            </div>
           
        </aside>
        
        <main class = 'mainContent'>
            <Home class = 'containerPage'  />
            <Historic class = 'containerPage' style="display: none" />
            <Scoreboard class = 'containerPage' style="display: none"/>
            <Presets class = 'containerPage' style="display: none" />
            <EditPreset class = 'containerPage' style="display: none"/>
            <RoberyNotify class = 'containerPage' style="display: none"/>
            <Robberies class = 'containerPage' style="display: none" />
            <div class = 'backStyle'></div>
        </main>
    </div>
</template>

<script>

import Home from './components/Home.vue'
import Historic from './components/Historic.vue'
import Scoreboard from './components/Scoreboard.vue'
import Presets from './components/Presets.vue'
import EditPreset from './components/EditPreset.vue'
import RoberyNotify from './components/RoberyNotify.vue'
import Robberies from './components/Robberies.vue'

export default {
    data() {
        return {
            menus: [
                {image: 'house.svg', element: 'adminHomePage', class: 'menuBttn activeMenuBttn', function: 'no'},
                {image: 'historic.svg', element: 'historicPage', class: 'menuBttn',  function: 'getHistoric'},
                {image: 'bell.svg', element: 'roberyNotifyAdmPage', class: 'menuBttn',  function: 'getAdminNotifies'},
                {image: 'wrench.svg', element: 'presetPage', class: 'menuBttn',  function: 'getPresets'},
                {image: 'robberies.svg', element: 'roberyPage', class: 'menuBttn',  function: 'getRobberies'}
            ]
        }
    },
    components: {
        Home,
        Historic,
        Scoreboard,
        Presets,
        EditPreset,
        RoberyNotify,
        Robberies
    },
    methods: {
        changeMenu(event, containerClass, menu_Function) {
            this.$store.state.Functions.changeMenu(event, containerClass)

            this.$store.commit(menu_Function)
        },

        closePainel() {
            this.$store.state.Functions.hidePainel('adminPainel')

            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": "closePainel"})
            }

            fetch(this.$store.state.url + '/suricato_robberies_API', options) 

        }
    },

    mounted() {
        
    }
}
</script>

