<template>
    <div class = 'main-container renderPage'>
        <div class = 'content-container'>
            <header>
                <h1> Rendição </h1>
            </header>

            <main>
                <div class = 'renderVote-container'>
                    <h1>Você deseja iniciar votação para seu time se render?</h1>

                    <div class = 'voteButton' @click = 'surrender()'>
                        <p>Iniciar votação</p>
                        <img  class = 'flagIcon' :src="require(`../../assets/flag.svg`)" alt="">
                    </div>
                </div>
            </main>

        </div>
    </div>
</template>

<script>
export default {
    methods: {
        surrender() {
            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": "voteGiveUpinRobbery"})
            }

            fetch(this.$store.state.url + '/suricato_robberies_API', options)
            .then(async response => {
                let obj = await response.json()

                if (obj.status) {
                    this.$store.state.Functions.changePageGame('scoreboardPageGame')
                    this.$store.state.Functions.closeAllPainel()
                    
                    let options = {
                        method: 'POST',
                        body: JSON.stringify({"cb": "closePainel"})
                    }

                    fetch(this.$store.state.url + '/suricato_robberies_API', options) 

                    this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: obj.msg})

                } else {
                    this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: obj.msg})
                }
            })
 
        }
    },

    computed: {
        
    }
}
</script>

