<template>
    <div class = 'playersConfig-container'>
        <header>
            <h1 style="border-right: solid 1px rgba(255, 255, 255, 0.1)">Item</h1>
            <h1>Quantidade</h1>
        </header>

        <div class = 'preset' v-for = '(item, index) in preset_data.items' v-bind:key = 'index'>
            <input type="text" v-model = 'item.name' style="border-right: solid 1px rgba(255, 255, 255, 0.1)">
            <input type="number" v-model = 'item.amount'>
        </div>

        <footer>
            <h1 @click = 'addProjection()'>+</h1>
            <h1 @click = 'removeProjection()' style="margin-bottom: 8px">-</h1>
        </footer>
    </div>
</template>

<script>
    export default {
        props: {
            preset_data: Object
        },

        methods: {
            addProjection() {
                this.preset_data.items.push({name: "[ ITEM ]", amount: 0})
            },
            removeProjection() {
                this.preset_data.items.splice(-1)
            },
            
            disableProjection(event) {
                let element = event.currentTarget
                let index = element.dataset.index

                this.preset_data.projection[index].disable = !element.checked

            }
        },

        
    }
</script>

