<template>
    <div class = 'game-page page'>
         <aside class = 'menuAside'>
            <div class = 'buttonsMenu-container'>

                <div class="menuUnit" v-for = '(menu, index) in menus' v-bind:key = 'index' >
                    <div :class = 'menu.class' @click = 'changePage($event, menu.element)'>
                        <img :src="require(`../assets/` + menu.image)" alt="">
                    </div>
                </div>

            </div>

            <div class = 'exitBttn' @click = 'closePainel()'>
                <img  src="../assets/exit.svg"  alt="">
            </div>
           
        </aside>

        <main class = 'mainContent'>
            <Scoreboard class = 'scoreboardPageGame containerPageGame'/>
            <StartVote class = 'startVotePage containerPageGame' style="display: none"/>
            <Vote :votedPlayers = 'votedPlayers' :voted = 'voted' style="display: none" class = 'votePage containerPageGame' />
            <div class = 'backStyle'></div>
        </main>
    </div>
</template>

<script>

import Scoreboard from './components/Scoreboard'
import StartVote from './components/StartVote'
import Vote from './components/Vote'

export default {
    data() {
        return {
            menus: [
                {image: 'robberies.svg', element: 'scoreboardPageGame', class: 'menuBttn activeMenuBttnGame scoreboardMenuBttn'},
                {image: 'surrender.svg', element: 'surrenderPage', class: 'menuBttn surrenderMenuBttn'},
            ],
            votedPlayers: [],
            voted: false
        }
    },

    components: {
        Scoreboard,
        StartVote,
        Vote
    },

    methods: {
        closePainel() {
            this.$store.state.Functions.hidePainel('gamePainel')

            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": "closePainel"})
            }

            fetch(this.$store.state.url + '/suricato_robberies_API', options) 

            this.$store.state.Functions.changePageGame('scoreboardPageGame')

            this.backToScoreboard()
        },

        backToScoreboard() {
            let element = document.querySelector('.scoreboardMenuBttn')

            let old_menu = document.querySelector('.surrenderMenuBttn')
            old_menu.classList.remove('activeMenuBttnGame')
            element.classList.add('activeMenuBttnGame')

            this.menus = [
                {image: 'robberies.svg', element: 'scoreboardPageGame', class: 'menuBttn activeMenuBttnGame scoreboardMenuBttn'},
                {image: 'surrender.svg', element: 'surrenderPage', class: 'menuBttn surrenderMenuBttn'},
            ]
        },

        changeMenu(event) {
            let element = event.currentTarget

            let old_menu = document.querySelector('.activeMenuBttnGame')
            old_menu.classList.remove('activeMenuBttnGame')
            element.classList.add('activeMenuBttnGame')
            
        },

        changePage(event, containerClass) {
            if (containerClass == 'surrenderPage') {
                let options = {
                    method: 'POST',
                    body: JSON.stringify({"cb": "getGiveUpInfos"})
                }

                fetch(this.$store.state.url + '/suricato_robberies_API', options)
                .then(async response => {
                    let obj = await response.json()


                    if (obj.playersAlreadyVoted.length == 0) {
                        this.$store.state.Functions.changePageGame('startVotePage')
                    } else {
                        this.votedPlayers = obj.playersAlreadyVoted
                        this.voted = obj.voted
                        this.$store.state.Functions.changePageGame('votePage')
                    }
                })

            } else {
                this.$store.state.Functions.changePageGame(containerClass)
            }

            this.changeMenu(event)
        },
    }


}
</script>

<style>

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background: #ffffff00;
    border-radius: 100px;

}

::-webkit-scrollbar-thumb {
    background: #1f1d2b00;
    border: solid 1px rgba(255, 255, 255, 0.103);
    border-radius: 100px;
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(85, 85, 85, 0);
    border-radius: 100px;

}

.activeMenuBttnGame {
    background-color: #BD00FF;
    box-shadow: 0px 0px 15px rgba(189, 0, 255, 0.49);
}

.activeMenuBttnGame img {
    filter: invert(1) grayscale(1) !important;
}
</style>
