<template>
    <div class = 'main-container actionPage'>
        <h1 class ='title'>Iniciar Defesa</h1>
        <div class = 'content-container' >
            <header >
                <h1>Participantes</h1>

                <div class = 'headerMenuInfos'>
                    <div class="specialInfo" >
                        <p>Banco Central</p>
                    </div>

                    <div class="menuInfo" style="width: 200px">
                        <p>Contingente neessário:</p>
                        <p>{{data.needcops}} oficiais</p>
                    </div>
                </div>
            </header>

            <main>
                <NoInvolvement :title = '"Sem Envolvimento"' :data = 'data'/>

                <Polices :title = '"Policiais Escalados"'  :data = 'data'/>
            </main>

            <div class = 'bttn-container'>
                <div class = 'cancelBttn bttn' @click = 'closePainel()'>
                    Cancelar
                </div>

                <div class = 'saveBttn bttn'  @click = 'initAction()'>
                    Iniciar
                </div>
            </div>
        </div>

       
    </div>
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.6.0/jquery.min.js" integrity="sha512-894YE6QWD5I59HgZOGReFYm4dnWc1Qt5NtvYSaNcOP+u1T9qYdvdihz0PPSiiqn/+/3e7Jo4EaG7TubfWGUrMQ==" crossorigin="anonymous" referrerpolicy="no-referrer"></script>
<script src="https://cdnjs.cloudflare.com/ajax/libs/jqueryui/1.12.1/jquery-ui.min.js" integrity="sha512-uto9mlQzrs59VwILcLiRYeLKPPbS/bT71da/OEBYEwcdNUk8jYIy+D176RYoop1Da+f9mvkYrmj5MCLZWEtQuA==" crossorigin="anonymous" referrerpolicy="no-referrer"></script>

<script>
// SCRIPTS
import DragConfig from './draggable/DragConfig'
import DropConfig from './draggable/DropConfig'


// COMPONENTES
import Polices from './policePlayers/Cops.vue'
import NoInvolvement from './policePlayers/NoInvolvement.vue'


export default {
    components: {
        Polices,
        NoInvolvement
    },

    props: {
        data: Object,
        current_key: String,
        name: String
    },

    mounted() {
        this.$nextTick(function () {
            setInterval(() => {
                $(".player").draggable(DragConfig.init());
                $(".playerUnit-container").droppable(DropConfig.init(this.data));
            }, 80);
        })
    },


    methods: {
        closePainel() {
            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": "closePainel", key: this.current_key})
            }
        
            fetch('http://suricato_robberies/suricato_robberies_API', options) 

            this.$store.state.Functions.closeAllPainel()
        },

        initAction() {
            if (this.data.cops.length < this.data.needcops) {
                this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: 'Você precisa inserir ' + this.data.needcops + ' Policiais.'})
                return 
            }

            let options = {
                method: 'POST',
                body: JSON.stringify({"cb": 'takeCopsForRob', key: this.current_key, cops: this.data.cops})
            }


            fetch(this.$store.state.url + '/suricato_robberies_API', options)
            .then(async response => {
                let obj = await response.json()


                if (obj.status) {
                    this.$root.$emit('messageNotify', {style: 'sucesso', sleep: 4000, content: obj.msg})
                } else {
                    this.$root.$emit('messageNotify', {style: 'negado', sleep: 4000, content: obj.msg})
                }

                let options = {
                    method: 'POST',
                    body: JSON.stringify({"cb": "closePainel"})
                }
        
                fetch('http://suricato_robberies/suricato_robberies_API', options) 


            })

            this.$store.state.Functions.closeAllPainel()
        }

    },

}
</script>

