<template>
    <div class = 'configUnit' >
        <input type="text" class = 'presetDataCheck' value = '122222not_value122222' :data-checked = 'preset_data.iswanted ? "1" : "0"' data-check_data_name = 'iswanted' hidden>

        <header>
            <h1 style="width: 100%">Nivel de Procurado</h1>
        </header>

        <main>
            <div class = 'chooseUnit'  v-bind:class = '{activeOption: preset_data.iswanted}' @click = 'changeCheckSettings($event, "1")'>
                <img :src="require(`../../../assets/arrow.svg`)" alt="">
            </div>

            <div class = 'chooseUnit' v-bind:class = '{activeOption: !preset_data.iswanted}' @click = 'changeCheckSettings($event, "2")'>
                <img style = 'height: 50px' :src="require(`../../../assets/error.svg`)"  alt="" data-type =  'cancel' >
            </div>
        </main>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                publicPath: process.env.BASE_URL
            }
        },

        props: {
            preset_data: Object
        },  

        methods: {
            changeCheckSettings(event, checkOption) {
                if (checkOption == '1') {
                    this.preset_data.iswanted = true

                } else {
                    this.preset_data.iswanted = false
                }
            },
        }
    }
</script>

