<template>
    <div class = 'hitMarker' style="display: none">
        <img :src="require('../assets/aim.svg')" alt="">
    </div>
</template>

<script>
export default {
    mounted() {
        this.listener = window.addEventListener('message', function({data}) {
            if (data.action == 'Hitmarker') {
                let element = document.querySelector('.hitMarker')
                element.style.display = 'block'

                setTimeout(() => {
                    element.style.display = 'none'
                }, 600)
            }
        }, false);
    }
}
</script>

<style scoped>
.hitMarker {
    height: 15px;
    width: 15px;
    border-radius: 100px;
    position: absolute;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: -15px;
    margin-left: -15px;
}

.hitMarker img {
    height: 30px;
}
</style>