<template>
    <div class = 'main-container roberyNotifyAdmPage'>
        <h1 class ='title'>Aceitar Ações</h1>
        <div class="content-container">
            <header>
                <h1>Notificações</h1>
            </header>

            <div class = 'notifiesAdm-container'>
                <div class = 'notifyAdmUnit' v-for = '(data, index) in notify_roberies' :key = 'index'  >
                    <div class = 'notifyAdmUnit-data' style="background-color: #361955;">
                        {{data.name}}
                    </div>

                    <div class = 'notifyAdmUnit-data'  >
                        Policias: {{data.cops}}
                    </div>

                    <div class = 'notifyAdmUnit-data'>
                        Invasores: {{data.bandits}}
                    </div>

                    <div class = 'notifyAdmUnit-data' >
                        <div class = 'chooseUnit' @click = 'confirmNotify("true", data.key, index)'>
                            <img :src="require(`../../assets/arrow.svg`)"  alt="">
                        </div>

                        <div class = 'chooseUnit' @click = 'confirmNotify("false", data.key, index)'  >
                            <img style = 'height: 40px' :src="require(`../../assets/error.svg`)"  alt="">
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                
            }
        },

        computed: {
            notify_roberies() {
                return this.$store.state.admin.notify_roberies
            }
        },

        methods: {
            confirmNotify(action, index, notify_obj_index) {
                let options = {
                    method: 'POST',
                    body: JSON.stringify({"cb": "responseAdminNotify", status: action, key: index})
                }

                fetch(this.$store.state.url + '/suricato_robberies_API', options)
                .then(() => {
                    this.$store.commit('removeAdminNotify', notify_obj_index)
                })
            }
        }
    }
</script>

