export default {
    init() {
        let dragConfig = {
            start(event) {
                let element = event.currentTarget
                element.style.opacity = '50%'
            },
            
            stop(event) {
                let element = event.target
                element.style.opacity = '100%'
            },
        
            helper : "clone", 
            appendTo: 'body',

        }

        return dragConfig
    }
}