<script>
    export default {
        data() {
            return {
                robbery: {},
                team: ""

            }
        },

        methods: {
            closePainel() {
                let options = {
                    method: 'POST',
                    body: JSON.stringify({"cb": "closePainel"})
                }
            
                fetch('http://suricato_robberies/suricato_robberies_API', options) 

                this.$store.state.Functions.closeAllPainel()
            },
        },  

        mounted() {
            this.listener = window.addEventListener('message', ({data}) => {
                if (data.action == 'OpenScoreboard') {
                    this.$store.state.Functions.showPainel('gamePainel')
                    this.robbery = data.robbery
                    this.team = data.team
                }
            }, false)
        }
    }
</script>

