<template>
    <div class = 'container' v-if = 'show'>
        <div class = 'specContainer'>
            <p class = 'title'>Espectando</p>

            <main v-bind:class = '{justifyCenter: admin}'>
                <img v-if = '!admin' class = 'arrows-icons' :src="require('./../assets/specArrow.svg')" alt="">
                <h1>{{name}}</h1>
                <img v-if = '!admin' class = 'arrows-icons' :src="require('./../assets/specArrow.svg')" style = 'transform: rotate(180deg)'>
            </main>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            name: 'Xaulin Matador',
            show: false,
            admin: false
        }
    },

    mounted() {
        this.listener = window.addEventListener('message', ({data}) => {
            if (data.action == 'Spectating') {
                if (data.player == false) {
                    this.show = false
                } else if (data.player) {
                    this.show = true
                    this.name = data.player.name

                    if (data.admin) {
                        this.admin = true
                    } else {
                        this.admin = false
                    }
                }
            }

        }, false);
    }
}
</script>

