<script>
    export default {
        data() {
            return {
                
            }
        },

        methods: {
            getActionPlayers(name, index, team, status) {
                this.$store.commit('changeScoreboardPlayers', {index, team})
                this.$store.commit('changeScoreboardName', name)
                this.$store.commit('changeScoreboardStatus', status)
                this.$store.state.Functions.changePage('scoreboardPage')
            }
        },

        computed: {
            historics() {
                return this.$store.state.admin.actions_historics
            }
        }
    }
</script>

